import { BrowserRouter, Route, Switch } from "react-router-dom";

import About from "components/pages/About";
import Bdage from "components/pages/Badge";

function Router() {
  return (
    <BrowserRouter>
      <Switch>
        {/* 노션용 뱃지 */}
        <Route path="/badge/:company/:number">
          <Bdage />
        </Route>

        {/* 최초 페이지 */}
        <Route path="/">
          <About />
        </Route>
      </Switch>
    </BrowserRouter>
  );
}

export default Router;
